/*

// ***** Clarion pattern ***** 

1. abstracts : variables, mixins, functions
2. base : base, reset, font-face, typography...
3. vendors : bootstrap, aos...
4. elements : base reset (button, link, form...)
5. components : buttons, search box, navbar, carousel...
6. layout : header, footer, grid, navigation, forms...
7. pages : home, contact...
8. utlities : helpers class
*/


