// ***** 7-1 pattern ***** 

1. abstracts : variables, mixins, functions
2. vendors : bootstrap, aos...
3. base : base, reset, font-face, typography, (animations)...
4. layout : header, footer, grid, navigation, forms...
5. components : buttons, search box, navbar, carousel...
6. pages : home, contact...
(7. themes : theme, admin)
(8. shame : shame)

*/


/*




@mixin screen-reader-only {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;  // optional
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.sr-only {
  @include screen-reader-only();
}



